import axios from 'axios'
export default {
    async asyncData({ $config }) {
        const { data } = await axios.get(`${$config.apiUrl}/information`, {
            headers: { 'X-API-KEY': $config.apiKey },
        })
        return {
            items: data.contents,
        }
    },
    head() {
        return {
            title: 'お知らせ',
        }
    },
}
